          </v-card>

          <!-- 需要折叠的部分 -->
          <v-card
            class="mb-3 mx-auto rounded-lg"
            width="100%"
            flat
            rounded
            v-if="item.filterLevel==1"
            @click="$router.push({
          path:'/postDetail',
