                </div>
                <span class="text_small-- text_grey mt-n2">{{timeFormat(item.latest_comment_time)}}</span>
              </span>
              <v-spacer></v-spacer>
              <v-btn icon @click.stop="showBottomDrawer(i)">
                <v-icon size="30px">mdi-dots-vertical</v-icon>
              </v-btn>
            </v-card-title>
            <v-card-text class="pb-0">
              <span class="mt-1 d-flex pb-0" style="width:100%;height:120px" v-if="false">
                <v-img
                  width="190px"
                  max-width="60vw"
                  :src="require('../../static/images/鲸鱼2.png')"
                  contain
                  style="  position: absolute;"
                  class="card_background_1"
                ></v-img>

                <span class="fold_text d-flex flex-column">
                  <span>
                    <span class="text_thick red--text text_xxlarge">已</span>
                    <span class="text_thick text_large">折叠</span>
                    <v-btn fab x-small text class="mb-3 pa-0">
                      <v-icon
                        size="20px"
                        color="black"
                        @click.stop="openWhyFoldedDialog=true"
                      >mdi-help-circle</v-icon>
                    </v-btn>
                  </span>
                  <span>
                    <v-chip class="mt-n1 mb-1" color="primary" outlined small>
                      <v-icon left size="20px">{{map.tagToIcon[item.tag]}}</v-icon>
                      <span>{{item.tag}}</span>
                    </v-chip>
                  </span>
                  <span class="text_grey text_small-- line_three pr-1">{{item.title}}</span>
                </span>
              </span>
              <div class="d-flex flex-column">
                <div class="d-flex align-center">
                  <div class="text-decoration-line-through text_small text_thick black--text">【已折叠 】</div>
                  <v-btn fab x-small text class="pa-0">
                    <v-icon
                      size="20px"
                      color="black"
                      @click.stop="openWhyFoldedDialog=true"
                    >mdi-help-circle</v-icon>
                  </v-btn>
                </div>
                <div>
                  <v-expansion-panels flat>
                    <v-expansion-panel>
                      <v-expansion-panel-header @click.stop class="d-flex justify-start">
                        <template v-slot:actions>
                          <v-icon size="30px" class="mt-n2">mdi-chevron-down</v-icon>
                        </template>
                        <div :style="item.tag.length>2?'width:35px':'width:0px'">
                          <v-chip class="mt-n1 mb-1" color="black" outlined small>
                            <v-icon left size="20px">{{map.tagToIcon[item.tag]}}</v-icon>
                            <span>{{item.tag}}</span>
                          </v-chip>
                        </div>
                        <div style="width:60px">
                          <v-chip class="mt-n1 mb-1" color="primary" outlined small>
                            <v-icon left size="20px">{{map.categoryToAvatar[item.category]}}</v-icon>
                            <span>{{item.category}}</span>
                          </v-chip>
                        </div>
                        <v-spacer></v-spacer>
                      </v-expansion-panel-header>
                      <v-expansion-panel-content
                        color="grey lighten-4"
                        class="mt-n3"
                        style="border-radius:10px;overflow:hidden"
                      >
                        <div class="d-flex flex-column">
                          <span class="text_thick black--text text_small line_one">{{item.title}}</span>
                          <span class="text_small--- grey--text line_three">{{item.content}}</span>
                        </div>
                      </v-expansion-panel-content>
                    </v-expansion-panel>
                  </v-expansion-panels>
                </div>
              </div>
              <div class="d-flex align-center mt-n1">
                <v-btn icon small @click.stop="likeOrDislike(i,1)">
                  <v-icon size="22px" color="black">mdi-thumb-up-outline</v-icon>
                </v-btn>
                <span class="ml-1">{{item.like_num}}</span>

                <v-avatar icon size="40px" class="ml-5">
                  <v-icon size="22px" color="black">mdi-eye-outline</v-icon>
                </v-avatar>
                <span>{{item.visit_num}}</span>
                <v-avatar icon size="40px" class="ml-6">
                  <v-icon size="22px" color="black">mdi-comment-processing-outline</v-icon>
                </v-avatar>
                <span>{{item.comment_num}}</span>
              </div>
            </v-card-text>
          </v-card>
        </div>
      </mescroll-vue>
    </div>
    <!-- 对折叠的解释 -->
    <v-dialog
      v-model="openWhyFoldedDialog"
      :overlay="false"
      max-width="500px"
      transition="dialog-transition"
      dark
    >
      <v-card style="background-color:#363636">
        <v-card-title>
          <v-icon>mdi-help-circle</v-icon>
          <span class="ml-1 white--text">『火锅底料』的折叠机制</span>
        </v-card-title>
        <v-card-text>
          <span>『火锅底料』社区是一个以匿名发言为主的社区,</span>
          <span>
            但是可以想见的是,匿名带来的自由,可能会滑向我们所不可控的方向。
            <br />
          </span>
          <span>我们希望为大家提供畅所欲言的平台,</span>
          <span>
            <strong>但是平台的健康运行,需要我们共同的努力。</strong>
            <br />
          </span>
          <span>
            原则上,『火锅底料』会
            <strong>避免删帖</strong> ，
            <br />
          </span>
          <span>
            对于一些『一般人价值判断』下有争议性的话题,
            <strong>则会折叠处理</strong> 。
            <br />
          </span>
          <span>
            希望大家在发"性话题","引战话题","令人不适话题","政治相关话题","未经证实话题",的时候,
            <strong>自觉添加上标签</strong>
            <br />
          </span>
          <span>
            <strong>我们会默认折叠带上标签的内容</strong>
          </span>
          <br />
          <span>
            您也可以在个人中心-我的设置中
            <strong>自定义过滤方式</strong>
          </span>
        </v-card-text>
      </v-card>
    </v-dialog>

    <!-- 弹窗 -->
    <v-snackbar v-model="snackbar.show" top :color="snackbar.color" app>
      {{ snackbar.message }}
      <template v-slot:action="{ attrs }">
        <v-btn color="white" text v-bind="attrs" @click="snackbar.show = false">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </template>
    </v-snackbar>
    <!-- 底部抽屉 -->
